import Vue from 'vue'
import VueRouter from 'vue-router'
// 引入首页、分类、购物车、用户信息
import home from '../views/home/home.vue'
import classfly from '../views/classfly/classfly.vue'
import my from '../views/my/my.vue'
import shopcar from '../views/shopcar/shopcar.vue'
import poductlist from '../views/poductlist/poductlist.vue'
import ProductDetail from '@/views/product-detail/product-detail.vue'
import Login from '@/views/login/login'
import Profile from '@/views/profile/profile.vue'
Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/home' },
  { path: '/classfly', name: 'classfly', component: classfly, meta: { index: 1 } },
  { path: '/home', name: 'home', component: home, meta: { index: 1 } },
  { path: '/shopcar', name: 'shopcar', component: shopcar, meta: { auth: true, index: 1 } },
  { path: '/my', name: 'my', component: my, meta: { index: 1, auth: true } },
  { path: '/poductlist/:categoryId', component: poductlist, meta: { index: 2 } },
  { path: '/product-detail/:id', component: ProductDetail, meta: { index: 3 } },
  { path: '/login', component: Login, meta: { index: 3 } },
  { path: '/profile', component: Profile, meta: { index: 2, auth: true } }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})
router.beforeEach((to, from, next) => {
  console.log(to);

  // 先判断路由元信息 是否 需要登录才能访问
  if (to.meta.auth) {
    if (localStorage.getItem('isLogin')) {
      next()
    } else {
      next('/login')
    }
  } else {
    next()
  }
})

export default router
