import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import 'lib-flexible'
import axios from 'axios'
import { Tabbar, TabbarItem, Icon, Swipe, 
  SwipeItem, Toast, Card, GoodsAction, 
  GoodsActionIcon, GoodsActionButton, Sku,
  Checkbox, Stepper, Divider, SubmitBar, SwipeCell, Button } from 'vant';
Vue.use(Tabbar).use(TabbarItem).use(Icon).use(Swipe).
use(SwipeItem).use(Toast).use(Card).use(GoodsAction).
use(GoodsActionIcon).use(GoodsActionButton).use(Sku).use(Checkbox).use(Stepper ).
use(Divider).use(SubmitBar).use(SwipeCell).use(Button);

Vue.config.productionTip = false
Vue.prototype.$http = axios;
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
