<template>
  <div>
    <z-header :showMore="true">购物车</z-header>
    <!-- 购物车 内容区域 -->
    <section class="shopcart-page">
      <!-- 购物车商品列表 -->
      <div class="shopcart-list">
        <shopcart-box v-for="(item, index) in this.$store.state.cartList" :key="item.id" :cart="item" :currentIndex="index"></shopcart-box>
      </div>

      <!-- 分割綫 -->
      <van-divider>可能你还想要</van-divider>
      <!-- 推荐 -->
      <recommend class="recommend"></recommend>
    </section>

    <!-- 提交栏 -->
    <van-submit-bar :price="$store.getters.getPrices * 100" button-text="提交订单" @submit="onSubmit">
      <!-- @change 当值发生改变 就会触发 changeChecked方法 -->
      <van-checkbox v-model="checked" @click="clickChecked">全选</van-checkbox>
    </van-submit-bar>

    <tab-bar />
  </div>
</template>

<script>
import tabBar from "@/components/tabBar.vue";
import zHeader from "@/components/common/z-header.vue";
import shopcartBox from "@/components/common/shopcart-box.vue";
import { catchCartList } from "@/api";
import recommend from "@/components/common/recommend";

export default {
  components: {
    tabBar,
    zHeader,
    shopcartBox,
    recommend
  },
  data() {
    return {
      // cartList: [],
      checked: false
    };
  },
  created() {
    this.getCartList();
  },
  updated() {
     this.checked = this.$store.getters.getAllChecked
  },

  methods: {
    // 触发 提交栏 复选框 值变化时的方法
    // @change 事件 是一旦值发生变化 就将 值设置给所有商品，存在一定的问题
    // 解决问题办法一： 只要是点击了 全选或者反选 才去 改变所有商品的值， 其他改变全选反选值的 情况不做处理
    clickChecked() {
      // 每次点击就改变 选中的状态
      this.checked = !this.checked

      // 当提交栏 的复选框选中 将 store中 所有的商品 都改为选中
      // 在 store 中提供修改商品是否选中 的方法
      this.$store.commit('updateGoods', this.checked)
    },
    // 提交按钮
    onSubmit() {
      console.log("点击了提交");
    },

    // 获取购物车商品数据
    getCartList() {
      catchCartList().then(res => {
        // console.log(res);
        // this.cartList = res;
        // 给每类 商品添加一个是否选中的属性
        let carts = res.filter(item => {
          item.checked = false;
          return item;
        });
        // console.log(carts);
        this.$store.commit("saveShopcarts", carts);
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.shopcart-page {
  position: relative;
  top: 88px;
  background-color: #f7f7f7;
  // height: 100%;
  .van-divider {
    background-color: #fff;
    padding: 40px 20px;
    border-color: #999;
  }
  // 解决 提交栏盖住商品信息
  .recommend {
    margin-bottom: 50px;
  }
}
.van-submit-bar {
  bottom: 100px;
}
</style>